	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Why Not Us?","date":"09 Jun 2005","slug":"/2005-06-09-why-not-us/","tags":"Y2005"},"html":"<p><a href=\"http://www.nytimes.com/2005/06/09/sports/baseball/09cubs.html?8hpib\" title=\"Red Sox Arrive to Remind Cubs Who's Left Holding Curse Now - New York Times\">Red Sox play in Wrigley Field for first time since 1918</a><br />\n<blockquote> In the middle of the sixth inning Tuesday night... the (black) cat bolted from its underground quarters, crossed in front of the Cubs' dugout and leapt into the third-base box seats before security guards could corral it... Cubs Manager Dusty Baker discounted the significance of the appearance of the black cat Tuesday night because it had white paws...</p></blockquote>\n<p>This is almost as weird as current German politics. Maybe Schr&ouml;der should start releasing black cats from the Kanzleramt.</p>"},"next":{"fields":{"title":"Where the US comes up short","date":"09 Jun 2005","slug":"/2005-06-09-where-the-us-comes-up-short/"}},"previous":{"fields":{"title":"'Grandpa Gang' bank robbers jailed","date":"10 Jun 2005","slug":"/2005-06-10--grandpa-gang-bank-robbers-jailed/"}}},{"node":{"fields":{"title":"Where the US comes up short","date":"09 Jun 2005","slug":"/2005-06-09-where-the-us-comes-up-short/","tags":"Y2005"},"html":"<p><a href=\"http://www.davosnewbies.com/2005/06/07/where-the-us-comes-up-short/\" title=\"Davos Newbies: Where the US comes up short\">Davos Newbies: Where the US comes up short</a> \"In so many ways, American life is very easy (for those of us fortunate enough to have jobs and money). Things work smoothly and technology is generally advanced. That's to be expected in the richest nation on earth. What's not expected is the number of areas where US technology compares woefully to poor, benighted Britain: the structure of mobile telephony, the comparative underdevelopment of digital television, the absence of digital radio and congestion charging are the four things that spring immediately to mind.\"</p>"},"next":{"fields":{"title":"Turning Germany into Delaware","date":"09 Jun 2005","slug":"/2005-06-09-turning-germany-into-delaware/"}},"previous":{"fields":{"title":"Why Not Us?","date":"09 Jun 2005","slug":"/2005-06-09-why-not-us/"}}},{"node":{"fields":{"title":"Turning Germany into Delaware","date":"09 Jun 2005","slug":"/2005-06-09-turning-germany-into-delaware/","tags":"Y2005"},"html":"<p><a href=\"http://q.queso.com/archives/001680\" title=\"QDN: Dotster%u2019s turning Germany into Delaware\">.de domains for Delaware</a>. <a href=\"http://wilmington.dot.de/\" title=\"http://www.Wilmington.Delaware\">Really</a>? Well, no, actually <a href=\"http://www.ci.wilmington.de.us\">not</a>.</p>"},"next":{"fields":{"title":"McDonald's Europe Names New President","date":"07 Jun 2005","slug":"/2005-06-07-mcdonalds-europe-names-new-president/"}},"previous":{"fields":{"title":"Where the US comes up short","date":"09 Jun 2005","slug":"/2005-06-09-where-the-us-comes-up-short/"}}}],"pathPrefix":"","first":false,"last":false,"index":306,"pageCount":1101}}

